import React, { Component } from "react";
import { withRouter, Link, Redirect } from "react-router-dom";
// import FacebookSocialAuth from "./FacebookLogin";
import GoogleSocialAuth from "../GoogleLogin";
import { Control, Errors, LocalForm } from "react-redux-form";
import Button from "@material-ui/core/Button";
import CloseRoundedIcon from "@material-ui/icons/CloseRounded";
import { CircularProgress } from "@material-ui/core";
import "./Login.css";

const required = (val) => val && val.length;
const validEmail = (val) =>
  /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(val);

class Login extends Component {
  constructor() {
    super();
    this.modalRef = React.createRef();
    this.state = {
      hidden: true,
      username: "",
      forgot: false,
      loginSubmitted: false,
      forgotSubmitted: false,
    };
    this.toggleShow = this.toggleShow.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.onSuccessLogin = this.onSuccessLogin.bind(this);
    this.onFailedLogin = this.onFailedLogin.bind(this);
    this.googleCallback = this.googleCallback.bind(this);
    this.handleForgot = this.handleForgot.bind(this);
    this.onSuccessForgot = this.onSuccessForgot.bind(this);
    // this.facebookCallback = this.facebookCallback.bind(this);
  }

  handleSubmit(values) {
    this.setState({
      loginSubmitted: true,
    });
    console.log(this.props.prevLocation);
    let pathname =
      this.props.prevLocation.pathname &&
      this.props.prevLocation.pathname !== "/login"
        ? this.props.prevLocation.pathname
        : "/";
    this.props.authLogin(
      values.username,
      values.password,
      pathname,
      this.onSuccessLogin,
      this.onFailedLogin
    );
    this.setState({
      username: values.username,
    });
  }

  onSuccessLogin(pathname) {
    this.setState({
      loginSubmitted: false,
    });
    console.log(pathname);
    if (pathname === undefined || pathname === "/home") {
      this.props.history.push("/profile");
    } else {
      this.props.history.push(pathname);
    }
  }

  onFailedLogin() {
    this.setState({
      loginSubmitted: false,
    });
    // this.props.history.push("/login");
  }

  toggleShow() {
    this.setState({ hidden: !this.state.hidden });
  }

  navigatePrev() {
    this.props.history.push(this.props.prevLocation.pathname);
  }

  googleCallback(accesstoken) {
    console.log(accesstoken);
    console.log(accesstoken.accessToken);
    console.log(this.props.prevLocation.pathname);
    this.props.googleLogin(
      accesstoken.accessToken,
      this.onSuccessLogin,
      this.onFailedLogin,
      this.props.prevLocation.pathname
    );
  }

  onSuccessForgot() {
    this.props.history.push("/");
    this.setState({
      forgotSubmitted: false,
    });
  }

  handleForgot(values) {
    console.log(values);
    this.setState({
      forgotSubmitted: true,
    });
    this.props.forgotPass(values.email, this.onSuccessForgot);
  }

  // facebookCallback(accesstoken) {
  //   console.log(accesstoken);
  //   console.log(accesstoken.accessToken);
  //   this.props.facebookLogin(
  //     accesstoken.accessToken,
  //     this.onSuccessLogin,
  //     this.onFailedLogin
  //   );
  // }

  render() {
    if (this.state.forgot) {
      return (
        <div className="overlay modal-wrapper fluid-container">
          <div className="popup">
            <div className="container" style={{ padding: 20 }}>
              <div className="d-flex border-bottom">
                <h3 style={{ textAlign: "center" }}>
                  <strong>Forgot Password? </strong>
                </h3>
                {console.log(
                  this.props.prevLocation.pathname === "/login",
                  this.props.redirected
                )}
                <Link
                  onClick={() => {
                    this.props.prevLocation.pathname === "/login" ||
                    this.props.redirected
                      ? this.props.history.push("/")
                      : this.props.history.push(this.props.prevLocation);
                  }}
                  className="ml-auto"
                  style={{ color: "black" }}
                >
                  <CloseRoundedIcon />
                  {/* <span className="fa fa-times fa-lg"></span> */}
                </Link>
              </div>
              <div style={{ marginTop: 20 }}>
                <LocalForm onSubmit={(values) => this.handleForgot(values)}>
                  <div className="row form-group">
                    <div className="col col-12">
                      <label htmlFor="username">
                        Enter you email address below and we will send you a
                        link to reset your password
                      </label>
                    </div>
                    <div className="col col-12">
                      <Control
                        type="text"
                        model=".email"
                        id="email"
                        name="email"
                        placeholder="email"
                        className="form-control"
                        style={{ height: 50 }}
                        validators={{
                          required,
                          validEmail,
                        }}
                      />
                      <Errors
                        className="text-danger error"
                        model=".email"
                        show="touched"
                        messages={{
                          required: "Required",
                          validEmail: "Invalid Email Address",
                        }}
                      />
                    </div>
                  </div>
                  <div className="row form-group">
                    <div className="col col-12">
                      <Button
                        variant="contained"
                        type="submit"
                        fullWidth={true}
                        style={{
                          backgroundColor: "#09bfad",
                          color: "white",
                          outline: "none",
                          height: 50,
                        }}
                      >
                        {this.state.forgotSubmitted ? (
                          <CircularProgress style={{ color: "white" }} />
                        ) : (
                          "Submit"
                        )}
                      </Button>
                    </div>
                  </div>
                </LocalForm>
              </div>
            </div>
          </div>
        </div>
      );
    } else if (!localStorage.getItem("token")) {
      return (
        <div className="overlay modal-wrapper fluid-container">
          {console.log(
            this.props.prevLocation.pathname === "/login",
            this.props.redirected
          )}
          <div className="popup">
            <div className="container" style={{ padding: 20 }}>
              <div className="d-flex border-bottom">
                <h3 style={{ textAlign: "center" }}>
                  <strong>Login</strong>
                </h3>
                <Link
                  onClick={() => {
                    this.props.prevLocation.pathname === "/login" ||
                    this.props.redirected
                      ? this.props.history.push("/")
                      : this.props.history.push(this.props.prevLocation);
                  }}
                  className="ml-auto"
                  style={{ color: "black" }}
                >
                  <CloseRoundedIcon />
                  {/* <span className="fa fa-times fa-lg"></span> */}
                </Link>
              </div>
              <div className="border-bottom">
                {/* <FacebookSocialAuth facebookCallback={this.facebookCallback} /> */}
                <GoogleSocialAuth googleCallback={this.googleCallback} />
              </div>
              <LocalForm onSubmit={(values) => this.handleSubmit(values)}>
                <div className="row form-group">
                  <div className="col col-12">
                    <label htmlFor="username">Email or Username</label>
                  </div>
                  <div className="col col-12">
                    <Control
                      type="text"
                      model=".username"
                      id="username"
                      name="username"
                      placeholder="username"
                      className="form-control"
                      style={{ height: 50 }}
                      validators={{
                        required,
                      }}
                    />
                    <Errors
                      className="text-danger error"
                      model=".username"
                      show="touched"
                      messages={{
                        required: "Required",
                      }}
                    />
                  </div>
                </div>
                <div className="row form-group">
                  <div className="col col-12">
                    <label htmlFor="password1">Password</label>
                  </div>
                  <div className="col col-12">
                    <div className="border password d-flex">
                      <Control
                        model=".password"
                        id="password"
                        name="password"
                        md={10}
                        placeholder="password"
                        className="form-control"
                        style={{ border: "none", height: 50 }}
                        type={this.state.hidden ? "password" : "text"}
                        validators={{
                          required,
                        }}
                      />
                      <span
                        role="button"
                        tabIndex={0}
                        onClick={this.toggleShow}
                        onKeyDown={this.toggleShow}
                        style={{
                          // marginTop: 4,
                          // marginLeft: 10,
                          outline: "none",
                          fontSize: 20,
                        }}
                        md={2}
                        className={
                          this.state.hidden
                            ? "fa fa-eye pt-3 pr-2"
                            : "fa fa-eye-slash pt-3 pr-2"
                        }
                        id="togglePassword1"
                      ></span>
                    </div>
                    <Errors
                      className="text-danger error"
                      model=".password1"
                      show="touched"
                      messages={{
                        required: "Required",
                      }}
                    />
                  </div>
                  <div className="text-muted ml-auto">
                    <Button
                      style={{ color: "#007bff" }}
                      onClick={() =>
                        this.setState({ forgot: !this.state.forgot })
                      }
                      variant="text"
                    >
                      <small>Forgot Password?</small>
                    </Button>
                    {/* <a style={{ paddingRight: 21 }} onClick={() => this.setState({ forgot: !this.state.forgot })} href="#">
                      Forgot Password?
                  </a> */}
                  </div>
                </div>
                <div className="row form-group">
                  <div className="col col-12">
                    <Button
                      variant="contained"
                      type="submit"
                      fullWidth={true}
                      style={{
                        backgroundColor: "#09bfad",
                        color: "white",
                        outline: "none",
                        height: 50,
                      }}
                    >
                      {this.state.loginSubmitted ? (
                        <CircularProgress style={{ color: "white" }} />
                      ) : (
                        "Login"
                      )}
                    </Button>
                  </div>
                </div>
              </LocalForm>
              <div className="border-top pt-3">
                <small className="text-muted">
                  Need An Account?&nbsp;
                  <Link
                    to={{
                      pathname: "/signup",
                      state: { modal: true, redirected: this.props.redirected },
                    }}
                  >
                    Create Account
                  </Link>
                </small>
              </div>
            </div>
          </div>
        </div>
      );
    } else {
      return <Redirect to="/home" />;
    }
  }
}

export default withRouter(Login);
