export default withRouter(CourseVideo);

const VideoSection = ({ currentVideo, open, onClickCard, introduction }) => {
  const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: playingAnimation,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };

  return (
    <section style={{ width: "100%" }} className={styles.videoSection}>
      <div className={styles.videoTitle}>
        <h2>{currentVideo.title}</h2>
      </div>
      <div
        style={{ background: open ? "white" : "black" }}
        className={styles.videoContainer}
      >
        <Vimeo
          video={currentVideo.src || "https://player.vimeo.com/video/501387150"}
          autoplay
          height={open ? 500 : 600}
          muted={false}
          controls={true}
          onEnd={() =>
            setTimeout(() => {
              onClickCard(
                introduction[1].videos[
                  introduction[1].videos.indexOf(currentVideo) + 1
                ]
              );
            }, 2000)
          }
        />
      </div>
      <div
        style={{ width: open ? "100%" : "50%" }}
        className={styles.videoList}
      >
        {introduction[1].videos.map((item, i) => {
          return (
            <div
              onClick={() => onClickCard(item)}
              key={item.title + i}
              className={styles.listItemVideo}
            >
              <div className={styles.videoAction}>
                <img
                  src={
                    item.type === "video"
                      ? item === currentVideo
                        ? pauseIcon
                        : playIcon
                      : bulbIcon
                  }
                  alt="video-action"
                />
              </div>
              <div
                onClick={() => onClickCard(item)}
                className={styles.videoDetail}
              >
                <h4>{item.title}</h4>
                {item === currentVideo ? (
                  <div className={styles.playingAnimation}>
                    <Lottie
                      options={defaultOptions}
                      height={40}
                      width={40}
                      isStopped={false}
                      isPaused={false}
                    />
                  </div>
                ) : item.complete ? (
                  <img src={checkIcon} alt="complete-robofreaks" />
                ) : (
                  ""
                )}
              </div>
            </div>
          );
        })}
      </div>
    </section>
  );
};

const CourseModules = ({ modules }) => {
  return (
    <div className={styles.modulesContainer}>
      {modules.map((item, i) => {
        return (
          <div
            key={item.title + "module" + i}
            className={classNames(
              styles.moduleCard,
              item.active ? styles.activeCard : "",
              item.locked ? styles.showLocked : ""
            )}
          >
            <div className={styles.moduleIcon}>
              <img src={item.icon} alt="module-icon" />
            </div>
            <div className={styles.content}>
              <div className={styles.details}>
                <h4>{item.title}</h4>
                <p>{item.description}</p>
                <div className={styles.items}>
                  <div className={styles.mediaItem}>
                    <img
                      src={item.active ? penIconWhite : penIcon}
                      alt="course-items"
                    />
                    {item.assignments}
                  </div>
                  <div className={styles.mediaItem}>
                    <img
                      src={item.active ? videoIconWhite : videoIcon}
                      alt="course-items"
                    />
                    {item.videos.length}
                  </div>
                  <div className={styles.mediaItem}>
                    <img
                      src={item.active ? listIconWhite : listIcon}
                      alt="course-items"
                    />
                    {item.quizes}
                  </div>
                </div>
              </div>
            </div>
            {item.coins && (
              <div className={styles.coins}>
                <p>{item.coins}</p>
                <img src={coinsIcon} alt="icons" />
              </div>
            )}
            {item.locked && (
              <>
                <div className={styles.locked}>
                  <img src={lockedIcon} alt="icons" />
                </div>
                <div className={styles.moduleLocked}>
                  <div className={styles.lockedHeading}>
                    <h5>This module is locked</h5>
                    <img src={lockedIcon} alt="locked-module" />
                  </div>
                  <div className={styles.coinsNeeded}>
                    <h3>
                      You need 320 Robocoins{" "}
                      <span>
                        <img src={coinsIcon} alt="coins" />
                      </span>{" "}
                      to unlock this course
                    </h3>
                  </div>
                </div>
              </>
            )}
          </div>
        );
      })}
    </div>
  );
};
