import React, { Component } from "react";
import { withRouter, Link, Redirect } from "react-router-dom";
// import FacebookSocialAuth from "./FacebookLogin";
import GoogleSocialAuth from "../GoogleLogin";
import { Control, Errors, LocalForm } from "react-redux-form";
import Button from "@material-ui/core/Button";
import CloseRoundedIcon from "@material-ui/icons/CloseRounded";
import { CircularProgress } from "@material-ui/core";
import "./Signup.css";

var pass1 = "";
const required = (val) => val && val.length;
const maxLength = (len) => (val) => !val || val.length <= len;
const minLength = (len) => (val) => val && val.length >= len;
const validEmail = (val) =>
  /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(val);
const validUsername = (val) => /^[a-zA-Z0-9_]+$/i.test(val);
const validPass = (val) =>
  /^(?=.*[a-z])(?=.*\d)(?=.*[$@$!%*#?&<>^()])[a-z\d$@$!%*#?&<>^()]{8,}$/i.test(
    val
  );
const matched = (val) => val === pass1;
const store = (val) => {
  pass1 = val;
  return true;
};

class SignUp extends Component {
  constructor() {
    super();
    this.modalRef = React.createRef();
    this.state = {
      hidden1: true,
      hidden2: true,
      open: false,
      signupSubmitted: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.toggleShow1 = this.toggleShow1.bind(this);
    this.toggleShow2 = this.toggleShow2.bind(this);
    this.onSuccessSignup = this.onSuccessSignup.bind(this);
    this.onFailedSignup = this.onFailedSignup.bind(this);
    this.onSuccessLogin = this.onSuccessLogin.bind(this);
    this.onFailedLogin = this.onFailedLogin.bind(this);
    this.googleCallback = this.googleCallback.bind(this);
    this.onSuccessGoogleSignUp = this.onSuccessGoogleSignUp.bind(this);
    // this.facebookCallback = this.facebookCallback.bind(this);
  }

  onSuccessSignup() {
    this.setState({
      signupSubmitted: false,
    });
    console.log("heeereeeee");
    this.props.history.push("/data");
  }

  onFailedSignup() {
    this.setState({
      signupSubmitted: false,
    });
  }

  handleSubmit(values) {
    this.setState({
      signupSubmitted: true,
    });
    console.log(values.name);
    let arr = values.name.split(" ");
    console.log(arr);
    console.log(arr[0], arr[1]);
    this.props.authSignup(
      values.username,
      values.email,
      values.password1,
      arr[0],
      arr[1],
      null,
      this.onSuccessSignup,
      this.onFailedSignup
    );
  }

  toggleShow1() {
    this.setState({ hidden1: !this.state.hidden1 });
  }

  toggleShow2() {
    this.setState({ hidden2: !this.state.hidden2 });
  }

  onSuccessLogin(pathname) {
    console.log(pathname);
    if (pathname === "/home" || pathname === undefined) {
      this.props.history.push("/profile");
    } else {
      this.props.history.push(pathname);
    }
  }

  onFailedLogin() {
    // this.props.history.push("/signup");
  }

  googleCallback(accesstoken) {
    this.props.googleLogin(
      accesstoken.accessToken,
      this.onSuccessGoogleSignUp,
      this.onFailedLogin
    );
  }

  onSuccessGoogleSignUp() {
    this.props.history.push("/data");
  }

  // facebookCallback(accesstoken) {
  //   console.log(accesstoken);
  //   this.props.facebookLogin(
  //     accesstoken.accessToken,
  //     this.onSuccessLogin,
  //     this.onFailedLogin
  //   );
  // }

  render() {
    if (!localStorage.getItem("token")) {
      return (
        <div className="overlay modal-wrapper fluid-container">
          {console.log(
            this.props.prevLocation.pathname === "/signup" ||
              this.props.redirected
          )}
          <div className="popup">
            <div className="container" style={{ padding: 20 }}>
              <div className="d-flex border-bottom">
                <h3 style={{ textAlign: "center" }}>
                  <strong>Create an Account</strong>
                </h3>
                <Link
                  onClick={() => {
                    this.props.prevLocation.pathname === "/signup" ||
                    this.props.redirected
                      ? this.props.history.push("/")
                      : this.props.history.push(this.props.prevLocation);
                  }}
                  className="ml-auto"
                  style={{ color: "black" }}
                >
                  <CloseRoundedIcon />
                  {/* <span className="fa fa-times fa-lg"></span> */}
                </Link>
              </div>
              <div className="border-bottom">
                {/* <FacebookSocialAuth facebookCallback={this.facebookCallback} /> */}
                <GoogleSocialAuth googleCallback={this.googleCallback} />
              </div>
              <LocalForm onSubmit={(values) => this.handleSubmit(values)}>
                <div className="row form-group">
                  <div className="col col-12">
                    <label htmlFor="username">Username</label>
                  </div>
                  <div className="col col-12">
                    <Control
                      type="text"
                      model=".username"
                      id="username"
                      name="username"
                      placeholder="username"
                      className="form-control"
                      style={{ height: 50 }}
                      validators={{
                        required,
                        validUsername,
                        minLength: minLength(3),
                        maxLength: maxLength(20),
                      }}
                    />
                    <Errors
                      className="text-danger error"
                      model=".username"
                      show="touched"
                      messages={{
                        required: "Required",
                        minLength: "Must be greater than 2 characters",
                        maxLength: "Must be 20 characters or less",
                        validUsername: "username not valid",
                      }}
                    />
                  </div>
                </div>
                <div className="row form-group">
                  <div className="col col-12">
                    <label htmlFor="email">Email</label>
                  </div>
                  <div className="col col-12">
                    <Control
                      type="text"
                      model=".email"
                      id="email"
                      name="email"
                      placeholder="Email"
                      className="form-control"
                      style={{ height: 50 }}
                      validators={{
                        required,
                        validEmail,
                      }}
                    />
                    <Errors
                      className="text-danger error"
                      model=".email"
                      show="touched"
                      messages={{
                        required: "Required ",
                        validEmail: "Invalid Email Address",
                      }}
                    />
                  </div>
                </div>
                <div className="row form-group">
                  <div className="col col-12">
                    <label htmlFor="name">Student's Name</label>
                  </div>
                  <div className="col col-12">
                    <Control
                      type="text"
                      model=".name"
                      id="name"
                      name="name"
                      placeholder="Student's Name"
                      className="form-control"
                      style={{ height: 50 }}
                      validators={{
                        required,
                      }}
                    />
                    <Errors
                      className="text-danger error"
                      model=".name"
                      show="touched"
                      messages={{
                        required: "Required ",
                      }}
                    />
                  </div>
                </div>
                <div className="row form-group">
                  <div className="col col-12">
                    <label htmlFor="password1">Password</label>
                  </div>
                  <div className="col col-12">
                    <div className="border password d-flex">
                      <Control
                        model=".password1"
                        id="password1"
                        name="password1"
                        placeholder="password"
                        md={10}
                        className="form-control"
                        style={{ border: "none", height: 50 }}
                        type={this.state.hidden1 ? "password" : "text"}
                        validators={{
                          required,
                          validPass,
                          store,
                        }}
                      />
                      <span
                        role="button"
                        tabIndex={0}
                        onClick={this.toggleShow1}
                        onKeyDown={this.toggleShow1}
                        style={{ outline: "none", fontSize: 20 }}
                        md={2}
                        className={
                          this.state.hidden1
                            ? "fa fa-eye pt-3 pr-2"
                            : "fa fa-eye-slash pt-3 pr-2"
                        }
                        id="togglePassword1"
                      ></span>
                    </div>
                    <Errors
                      className="text-danger error"
                      model=".password1"
                      show="touched"
                      messages={{
                        required: "Required ",
                        validPass:
                          "Must contain at least one numeric and special character",
                      }}
                    />
                  </div>
                </div>
                <div className="row form-group">
                  <div className="col col-12">
                    <label htmlFor="password2">Confirm Password</label>
                  </div>
                  <div className="col col-12">
                    <div className="border password d-flex">
                      <Control
                        model=".password2"
                        id="password2"
                        name="password2"
                        placeholder="Confirm Password"
                        md={10}
                        className="form-control"
                        style={{ border: "none", height: 50 }}
                        type={this.state.hidden2 ? "password" : "text"}
                        validators={{
                          required,
                          matched,
                        }}
                      />
                      <span
                        role="button"
                        tabIndex={0}
                        onClick={this.toggleShow2}
                        onKeyDown={this.toggleShow2}
                        style={{ outline: "none", fontSize: 20 }}
                        md={2}
                        className={
                          this.state.hidden2
                            ? "fa fa-eye pt-3 pr-2"
                            : "fa fa-eye-slash pt-3 pr-2"
                        }
                        id="togglePassword"
                      ></span>
                    </div>
                    <Errors
                      className="text-danger error"
                      model=".password2"
                      show="touched"
                      messages={{
                        required: "Required",
                        matched: "password does not match",
                      }}
                    />
                  </div>
                </div>
                <div className="row form-group">
                  <div className="col col-12">
                    <Button
                      variant="contained"
                      type="submit"
                      fullWidth={true}
                      style={{
                        backgroundColor: "#09bfad",
                        color: "white",
                        outline: "none",
                        height: 50,
                      }}
                    >
                      {this.state.signupSubmitted ? (
                        <CircularProgress style={{ color: "white" }} />
                      ) : (
                        "Sign Up"
                      )}
                    </Button>
                  </div>
                </div>
              </LocalForm>
              <div className="border-top pt-3">
                <small className="text-muted">
                  Already have an Account?&nbsp;
                  <Link
                    to={{
                      pathname: "/login",
                      state: { modal: true, redirected: this.props.redirected },
                    }}
                  >
                    Login
                  </Link>
                </small>
              </div>
            </div>
          </div>
        </div>
      );
    } else {
      return <Redirect to="/home" />;
    }
  }
}

export default withRouter(SignUp);
