export const submitQuiz = (answer, submitted) => (dispatch) => {
  // let cookiearray = document.cookie.split(";");
  // let csrf = "";
  // for (var i = 0; i < cookiearray.length; i++) {
  //   let name = cookiearray[i].split('=')[0];
  //   let value = cookiearray[i].split('=')[1];
  //   if (name === " csrftoken") {
  //     csrf = value
  //   }
  // }

  // let csrf = localStorage.getItem('csrf');
  const token = localStorage.getItem("token");
  let csrf = "";
  if (token) {
    console.log("token available");
    csrf = localStorage.getItem("csrf");
  }
  console.log("csrf", csrf);
  console.log("token", token);

  console.log(answer);
  const creds = JSON.stringify({
    id: answer.id,
    answers: answer.answers,
  });
  console.log(creds);
  return fetch(
    config.url.API_URL + `/api/coursework/quiz/submit-quiz/${answer.id}`,
    {
      method: "PATCH",
      body: creds,
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-CSRFToken": csrf,
        Authorization: "token " + token,
      },
    }
  )
    .then(
      (res) => {
        console.log(res);
        if (res.ok) {
          return res;
        } else {
          var error = new Error("Error " + res.status + ": " + res.statusText);
          error.res = res;
          throw error;
        }
      },
      (error) => {
        console.log(error);
        throw error;
      }
    )
    .then((res) => res.json())
    .then((quizResponse) => {
      console.log(quizResponse);
      dispatch(responseSuccess(quizResponse));
      submitted();
    })
    .catch((err) => {
      console.log(err);
      if (err.res) {
        err.res.json().then((res) => {
          console.log(res);
          dispatch(responseFail(res.detail));
        });
      }
    });
};

