export const contentDone = (content_id, modIdx, cntIdx, success, failed) => (
  dispatch
) => {
  const token = localStorage.getItem("token");
  let csrf = "";
  if (token) {
    console.log("token available");
    csrf = localStorage.getItem("csrf");
  }
  console.log("csrf", csrf);
  console.log("token", token);
  return fetch(config.url.API_URL + `/api/courses/mark-as-done/${content_id}`, {
    method: "PATCH",
    headers: {
      Authorization: "token " + token,
      "X-CSRFToken": csrf,
    },
  })
    .then(
      (res) => {
        console.log(res);
        if (res.ok) {
          return res;
        } else {
          var error = new Error("Error " + res.status + ": " + res.statusText);
          error.res = res;
          throw error;
        }
      },
      (error) => {
        console.log(error);
        throw error;
      }
    )
    .then((res) => {
      console.log(res);
      dispatch(progressSuccess(modIdx, cntIdx));
      success();
    })
    .catch((err) => {
      console.log(err);
      failed();
    });
};

